<template>
    <div class="wrapper">
        <div class="topTitle">
            <span>OMS</span>  在线阅卷系统
        </div>
        <div class="container">
            <div class="pic">
                <img src="/img/pic.png" alt="Alternate Text" />
            </div>
            <form  class="form">
                <div class="schoolLoge" style="text-align:center;margin-bottom:28px;height:32px;">
                    <img src="/img/schoolLogo/hc.png" alt="Alternate Text" />
                </div>
                <div class="row">
                    <input id="txt_account" type="text" placeholder="用户名">
                    <i class="fa fa-user"></i>
                </div>
                <div class="row">
                    <input id="txt_password" type="password" placeholder="登录密码">
                    <i class="fa fa-key"></i>
                </div>
                <div class="row">
                    <input id="txt_code" maxlength="4" type="text" placeholder="验证码" style="width: 190px; float: left;">
                    <div style="width: 90px; float: right; padding-top: 4px; padding-left: 20px;">
                        看不清？<br /><a id="switchCode" href="" style="text-decoration: none;" @click="switchCode">换一张</a>
                        <img id="imgcode" class="authcode" src="/Service/api/Login/GetAuthCode" width="80" height="25" />
                    </div>
                    
                </div>
                <div class="login_tips"></div>
                <div class="row">
                    <button id="login_button" type="button" @click="loginClick"><span>登录</span></button>
                </div>
                <div class="row">
                </div>
            </form>
        </div>
    </div>
    <div class="copyright">
        ©2015-2019 弘成科技发展有限公司版权所有 京ICP证041171号 京公网安备110101002605
    </div>
</template>

<script>
    var that;
    export default {
        name: 'Login',
        components: {
            
        },
        mounted(){
            that = this;
            that.init();
        },
        methods:{
            switchCode(){
                $("#imgcode").attr("src", "/Service/api/Login/GetAuthCode?time=" + Math.random());
            },
            init(){
                $('.wrapper').height($(window).height());
                $(".form").css("margin-top", ($(window).height()-80-400 )/2-15);
                if ($(window).width() > 1366) {

                    $(".container .pic").css({"margin-right":"200px"})
                } else {
                    $(".container .pic").css({ "margin-right": "100px" })
                }
                $(window).resize(function (e) {
                    $('.wrapper').height($(window).height());
                    $(".form").css("margin-top", ($(window).height() - 80 - 400) / 2-15);
                    if ($(window).width() > 1366) {
                        $(".container .pic").css({ "margin-right": "200px" })
                    } else {
                        $(".container .pic").css({ "margin-right": "100px" })
                    }
                });
                document.onkeydown = function (e) {
                    if (!e) e = window.event;
                    if ((e.keyCode || e.which) == 13) {
                        document.getElementById("login_button").focus();
                        document.getElementById("login_button").click();
                    }
                }
            },
            loginClick () {
                var $username = $("#txt_account");
                var $password = $("#txt_password");
                var $code = $("#txt_code");
                if ($username.val() == "") {
                    $username.focus();
                    this.formMessage('请输入用户名。');
                    return false;
                } else if ($password.val() == "") {
                    $password.focus();
                    this.formMessage('请输入登录密码。');
                    return false;
                } else if ($code.val() == "") {
                    $code.focus();
                    this.formMessage('请输入验证码。');
                    return false;
                } else {
                    $("#login_button").attr('disabled', 'disabled').find('span').html("loading...");
                    this.$axios.post('/service/api/Login/CheckLogin',{username: $.trim($username.val()), password: $.trim($password.val()), code: $.trim($code.val())})
                    .then(function(res){
                        var data = res.data;
                        if (data.status) {
                                window.localStorage["loginToke"] = data.data;
                                $("#login_button").find('span').html("登录成功，正在跳转...");
                                window.setTimeout(function () {
                                    that.$router.push("Home");
                                    //window.location.href = "/Home/Index";
                                }, 500);
                            } else {
                                $("#login_button").removeAttr('disabled').find('span').html("登录");
                                $("#switchCode").trigger("click");
                                $code.val('');
                                that.formMessage(data.message);
                            }
                    })
                }
            },
            formMessage(msg) {
                $('.login_tips').find('.tips_msg').remove();
                $('.login_tips').append('<div class="tips_msg"><i class="fa fa-question-circle" style="margin-top:10px;"></i>' + msg + '</div>');
            },
        }
    }
</script>

<style lang="scss" scoped>

.wrapper{
    background-image: url('/img/logBg.jpg');
    .topTitle{
        height: 80px;
        line-height: 80px;
        padding-left: 120px;
        font-size: 15px;
        font-weight: bold;
        color: #242834;
        background-color: #fff;
        span{
            font-size: 31px;
            color: #3B8EFF;
            font-weight: bold;
        }
    }
    .container{
        width: 100%;
        max-width: 100%;
        overflow: hidden;
        min-height: 80%;
        margin: 0 auto;
        text-align: center;
        position: relative;
        .pic{
            float: left;
        }
        .form{
            float: left;
            margin-left: auto;
            margin-right: auto;
            position: relative;
            z-index: 2;
            text-align: left;
            background-color: #fff;
            padding: 32px 60px;
            border-radius: 5px;
            box-shadow: 0 0 10px rgba(0,0,0,.1);
            width: 400px;
            height: 380px;
            input{
                font-size: 14px;
                outline: 0;
                border: 1px solid rgba(0,0,0,0.08);
                background-color: #fff;
                width: 280px;
                height: 40px;
                border-radius: 4px;
                padding: 12px 15px;
                margin-bottom: 32px;
                display: block;
                text-align: left;
                color: #929191;
                -webkit-transition-duration: 0.25s;
                transition-duration: 0.25s;
                font-weight: 300;
            }
            button{
                outline: 0;
                background-color: #3891ff;
                border: 0;
                padding: 8px 15px;
                color: #fff;
                border-radius: 5px;
                width: 280px;
                cursor: pointer;
                font: 16px/1.5 Lucida Grande,Lucida Sans Unicode,Helvetica Neue,Hiragino Sans GB,Microsoft Yahei,sans-serif;
            }
            i{
                color: #cbcbcb;
                top: 13px;
                right: 10px;
                z-index: 99;
                position: absolute;
                display: block;
                width: 30px;
                height: 30px;
                text-align: center;
                font-size: 18px !important;
            }
            .row{
                position: relative;
                overflow: hidden;
            }
            .login_tips {
                position: absolute;
                bottom: 10px;
                z-index: 99;
                width: 100%;
                .tips_msg {
                    height: 30px;
                    line-height: 30px;
                    color: red;
                    margin: 0 auto;
                    padding-top: 10px;
                    border-radius: 5px;
                    width: 300px;
                    font-size: 14px;
                    i {
                        position: relative;
                        top: 0.5px;
                        font-size: 15px;
                        padding-right: 5px;
                        display: inline;
                    }
                }
            }
        }
    }
}
.copyright{
    text-align: center;
    color: #fff;
    position: absolute;
    top: 100%;
    width: 100%;
    margin-top: -36px;
    font-family: Microsoft Yahei;
    line-height: 25px;
    font-size: 14px;
}
</style>